// telegramWebAppListener.ts

/**
 * 
 * @param obj 
 * @param path  "Telegram.WebApp"
 * @returns boolean
 */
function hasNestedProperty(obj: any, path: string): boolean {
  return path.split('.').every((key) => {
    if (obj && typeof obj === 'object' && key in obj) {
      obj = obj[key]
      return true
    }
    return false
  })
}

/**
 *  window.Telegram.WebApp 
 * @param callback  Telegram WebApp 
 * @param interval 
 * @param timeout 
 */
export function listenForTelegramWebApp(
  callback: (data: any) => void,
  onTimeout?: () => void,
  interval: number = 500,
  timeout: number = 5000
): void {
  if (!window || typeof window === 'undefined') {
    //  SSR 
    return
  }

  let observer: MutationObserver | null = null
  let polling: NodeJS.Timeout | null = null
  let elapsed: number = 0

  // 
  const targetPath = 'Telegram.WebApp'

  const cleanup = () => {
    if (observer) {
      observer.disconnect()
      observer = null
    }
    if (polling) {
      clearInterval(polling)
      polling = null
    }
  }

  const checkAndCallback = () => {
    if (hasNestedProperty(window, targetPath)) {
      callback((window as any)?.Telegram?.WebApp?.initDataUnsafe)
      cleanup()
    } else if (elapsed >= timeout) {
      // 
      console.warn(`Listen Telegram.WebApp timeout:${timeout}ms`)
      onTimeout?.()
      cleanup()
    }
  }

  // 
  checkAndCallback()

  if (hasNestedProperty(window, targetPath)) {
    // 
    return
  }

  //  MutationObserver  DOM 
  observer = new MutationObserver(() => {
    checkAndCallback()
  })

  // 
  observer.observe(document, { childList: true, subtree: true })

  // 
  polling = setInterval(() => {
    elapsed += interval
    checkAndCallback()
  }, interval)
}
